body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header{
    margin-bottom: 30px;
}

.brand-logo{
    margin-left: 20px;
}


.head-block{
    margin: 0 auto;
    text-align: center;
    width: 80%;
    background: #80cbc4;
    padding: 5px;
    color: black;
    font-weight: 600;
    border-radius: 0 0 20px 20px;
}
.edit{
    margin-top: 0;
    padding: 0;
}
.edit-block-title{
    padding-top: 5px;
    margin-top: 0;
}
.edit-title{

    font-size: 2em;
    padding: 5px;
}
.edit-label span{
    font-size: 1.5em;
    size: 2em;
}
.edit{
    color: darkslategray;
}
.edit input{
    color: #440078;
    font-weight: 500;
}
.edit label{
    font-weight: 600;
    font-size: 0.7em;
    color: #091125;
}
.del-btn-exit{
    background: rgba(100, 221, 23, 0.51);
}
.del-btn-exit:hover{
    background: #64dd17;
}
.del-btn-delete{
    background: rgba(255, 23, 68, 0.51);
}
.del-btn-delete:hover{
    background: #ff1744;
}

.title-del-modal{
    font-weight: 400;
}

.del-btn{
    font-size: 1.8em;
    float: left;
    width: 50%;
    padding-top: 15px;
    height: 63px;
}
.group-delete{
    color: #00c853;
}
.group-delete:hover{
    color: #ff0f26;
}
.add-user-form input{
    color: whitesmoke;
}
